/* -------------------------- Colorful Button -------------------------- */


.div-btn {
    margin: 0;
    padding: 30px;
    display: flex;
    justify-content: right;
}

.button {
    display: flex;
    justify-content: right;
    align-content: right;
    align-items: right;
    margin: 30px 0px 30px 0px;
    padding: 0;
    min-height: 30px;
    background-color: black;
}

.btn {
    position: relative;
    width: 250px;
    height: 60px;
    line-height: 60px;
    background-color: black;
    text-transform: uppercase;
    font-size: 23px;
    letter-spacing: 2px;
    text-decoration: none;
    -webkit-box-reflect: below 1px linear-gradient(transparent, #0004);
}

.btn::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(45deg, #ff00cc, #ffffff, #00fbff, #ff00cc, #ffffff, #da75ff, #ff03f7);
    background-size: 400%;
    opacity: 0;
    transition: 0.5s;
    -webkit-animation: animate 20s linear infinite;
            animation: animate 20s linear infinite;
}

.btn::after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(45deg, #ff00cc, #ffffff, #00f7ff, #ff00cc, #ffffff, #da75ff, #ff03f7);
    background-size: 400%;
    opacity: 0;
    -webkit-filter: blur(20px);
            filter: blur(20px);
    transition: 0.5s;
    -webkit-animation: animate 20s linear infinite;
            animation: animate 20s linear infinite;
}

.btn:hover::before,
.btn:hover::after {
    opacity: 1;
}

@-webkit-keyframes animate {
    0% {
        background-position: 0 0;
    }
    50% {
        background-position: 300% 0;
    }
    100% {
        background-position: 0 0;
    }
}

@keyframes animate {
    0% {
        background-position: 0 0;
    }
    50% {
        background-position: 300% 0;
    }
    100% {
        background-position: 0 0;
    }
}

.btn span {
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    text-align: center;
    background: #0c0c0c;
    color: white;
    transition: 0.5s;
    z-index: 1;
}

.btn:hover span {
    color: #E100FF;
}

.btn span::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 50%;
    background: rgba(255, 255, 255, 0.1);
}

